import { Component, h } from '@stencil/core';
import { account } from "../../../global/data/account";

@Component({
    tag: 'ks-points-history',
    styleUrl: 'pointsHistory.css'
})
export class PointsHistory {

    openOrder(index: string) {
        if(index) document.querySelector('ks-account-order-modal')?.show(index);
    }

	render() {
        const strings = account.get("strings");
        const points = account.get("points");

        return [
            <div class="row row-heading">
                <div>{strings.historyPoints}</div>
                <div>{strings.historyStatus}</div>
                <div>{strings.historyDate}</div>
                <div>{strings.historyAcceptedDate}</div>
                <div>{strings.historyDetails}</div>
            </div>,
            
            points.history.map(history =>
                <div class="row">
                    <div>{history.points}</div>
                    <div class={history.statusCategory}>{history.status}</div>
                    <div>{history.date}</div>
                    <div>{history.acceptedDate}</div>
                    <div class={history.detailsOrder ? "order" : ""}
                        onClick={()=>this.openOrder(history.detailsOrder)}
                    >
                        {history.details}
                    </div>
                </div>    
            )
        ];
	}
}










