import { Component, h, State, Element, Method } from '@stencil/core';
import { account, AccountOrder } from "../../../global/data/account";

@Component({
    tag: 'ks-account-return-dialog',
    styleUrl: 'accountReturnDialog.css'
})
export class AccountReturnDialog {
    @Element() root: HTMLElement;
    @State() currentOrder: AccountOrder;

    @Method() async show(orderNumber: string) {
        this.currentOrder = orderNumber ?
            account.get("orders").find(order => order.order == orderNumber) :
            null;

        this.root.querySelector('ks-dialog')?.show();
    }

    @Method() async hide() {
        this.root.querySelector('ks-dialog')?.hide();
    }

	render() {
        const strings = account.get("strings");
        const orders = account.get("orders");

        return <ks-dialog full class="return-add-dialog">
            <div class="form-wrapper">
                <div class="form">
                    <h1>{strings.returnOrder}</h1>

                    <div class="inputs">
                        {this.currentOrder ? 
                            <div class="return_order_preselected"> {strings.orderNumber}: {this.currentOrder.order} &emsp; {this.currentOrder.date} </div> :

                            <ks-input-select name="return_order"
                                label={strings.order}
                            >
                                {orders.map(order =>
                                    <option value={order.order}>
                                        {strings.orderNumber}: {order.order} &emsp; {order.date}
                                    </option>    
                                )}
                            </ks-input-select>
                        }

                        <ks-input-text name="return_title"
                            label={strings.returnTitle} icon={"type"} right-icon
                        />
                        <ks-input-textarea name="return_description"
                            label={strings.returnDescription} noresize rows={5}
                        />
                    </div>
                    
                    <ks-button submit class="submit" round
                        name={strings.returnSubmit}
                    />
                </div>
            </div>
        </ks-dialog>;
	}
}