import { Component, h, State, Element, Method } from '@stencil/core';
import { account, AccountOrder } from "../../../global/data/account";
import priceFormat from '../../../global/functions/price';

@Component({
    tag: 'ks-account-order-modal',
    styleUrl: 'accountOrderModal.css'
})
export class AccountOrderModal {
    @Element() root: HTMLElement;
    @State() currentOrder: AccountOrder;

    @Method() async show(orderNumber: string) {
        this.currentOrder = account.get("orders").find(order => order.order == orderNumber);

        if(this.currentOrder)
            this.root.querySelector('ks-dialog')?.show();
    }

    @Method() async hide() {
        this.root.querySelector('ks-dialog')?.hide();
    }

    showReturnDialog() {
        document.querySelector('ks-account-return-dialog')?.show(this.currentOrder.order);
        setTimeout(() => {
            this.root.querySelector('ks-dialog')?.hide();
        }, 300);
    }

	render() {
        const strings = account.get("strings");
        const currentOrder = this.currentOrder;

        return <ks-dialog full> {!currentOrder ? null :

            <div class="modal-wrapper">
                <div class="top">
                    <h1>{strings.order}: {currentOrder.order}</h1>
                    <p>{currentOrder.date}</p>
                </div>

                <div class="status-history">
                    {currentOrder.status.reverse().map(status =>
                        <div class="status-row">
                            <div class="status">{strings.status}: {status.name}</div>
                            <span>{status.date}</span>
                        </div>
                    )}
                </div>

                <div class="shipping">
                    <p>{strings.shippingMethod}: {currentOrder.shipping}</p>
                    <br/>
                    <p>{strings.shippingAddress}:</p>
                    <p>{currentOrder.shippingAddress}</p>
                </div>

                <div class="payment">
                    <p>{strings.paymentMethod}: {currentOrder.payment}</p>
                    <br/>
                    <p>{strings.invoiceAddress}:</p>
                    <p>{currentOrder.invoiceAddress}</p>
                </div>

                <div class="products">
                    <div class="heading">
                        <div class="image"/>
                        <div class="name"/>
                        <div class="count">{strings.count}</div>
                        <div class="price">{strings.value}</div>
                    </div>

                    {currentOrder.products.map(product => 
                        <a href={product.link} class="product">
                            <ks-img2 center class="image" src={product.image} />
                            <div class="name">{product.name}</div>
                            <div class="count">{product.count}</div>
                            <div class="price">{priceFormat(product.value)}</div>
                        </a>  
                    )}
                </div>

                <div class="summary">
                    {currentOrder.summary.map(entry =>
                        <div class="entry">
                            <div class="name">{entry.name}</div>
                            <div class="value">{priceFormat(entry.value)}</div>
                        </div>
                    )}
                    <div class="entry total">
                        <div class="name">{strings.total}</div>
                        <div class="value">{priceFormat(currentOrder.value)}</div>
                    </div>
                </div>

                <div class="buttons">
                    <ks-button submit class="submit" round border
                        name={strings.returnSubmit}
                        onClick={()=>this.showReturnDialog()}
                    />
                </div>
            </div>
    
        }</ks-dialog>;
	}
}