import { Component, h, Element, State } from '@stencil/core';
import { account, AccountReturn } from "../../../global/data/account";

@Component({
    tag: 'ks-account-returns',
    styleUrl: 'accountReturns.css'
})
export class AdditionalAdresses {
    @Element() root: HTMLKsAdditionalAdressesElement;

    showReturnDialog(order: string = null) {
        document.querySelector("ks-account-return-dialog").show(order);
    }

    showOrder(order: string = null) {
        document.querySelector('ks-account-order-modal')?.show(order);
        setTimeout(() => {
            (document.querySelector("ks-dialog.return-modal") as HTMLKsDialogElement).hide();
        }, 300);
    }

    @State() currentReturn: AccountReturn;
    showReturn(index: number) {
        this.currentReturn = account.get("returns")[index] || null;

        const dialog = this.root.querySelector("ks-dialog.return-modal") as HTMLKsDialogElement;
        dialog.show();
    }

	render() {
		const strings = account.get("strings");
        const returns = account.get("returns");

        return [
            <div class="entries">
                {returns.map((entry, index) => 
                    <div class="entry" onClick={()=>this.showReturn(index)}>
                        <div class="top">
                            <h2>{entry.title}</h2>
                        </div>
                        <div class="description">
                            <p>{strings.orderNumber}: {entry.order}</p>
                            <p>{strings.date}: {entry.date}</p>
                            <div class="status">{strings.status}: {entry?.status[entry.status.length-1].name}</div>
                        </div>
                    </div>
                )}

                {returns.length == 0 ? 
                    <div class="nocontent">{strings.noReturnEntries}</div>
                : null}
            </div>,

            <ks-button round left icon="corner-down-left" name={strings.returnOrder} onClick={()=>this.showReturnDialog()} />,

            <ks-dialog full class="return-modal">
                <div class="return-modal-wrapper">
                    {this.currentReturn ? [
                        <div class="flex">
                            <h1>{this.currentReturn.title}</h1>
                            <p class="status order" onClick={()=>this.showOrder(this.currentReturn.order)}>
                                {strings.orderNumber}: {this.currentReturn.order}
                            </p>
                        </div>,

                        <div class="flex">
                            <p>{this.currentReturn.date}</p>
                            <p>{strings.returnNumber}: {this.currentReturn.number}</p>
                        </div>,
                        
                        <div class="description">{this.currentReturn.description}</div>,

                        <div class="status-history">
                            {this.currentReturn.status.reverse().map(status =>
                                <div class="status-row">
                                    <div class="status">{strings.status}: {status.name}</div>
                                    <span>{status.date}</span>
                                </div>
                            )}
                        </div>
                    ] : null}
                </div>
            </ks-dialog>
        ];
	}
}










