import { Component, h, Element } from '@stencil/core';
import { account } from "../../../global/data/account";
import priceFormat from '../../../global/functions/price';

@Component({
    tag: 'ks-account-orders',
    styleUrl: 'accountOrders.css'
})
export class AccountOrders {
    @Element() root: HTMLElement;

    openOrder(order: string) {
        document.querySelector('ks-account-order-modal')?.show(order);
    }

	render() {
        const strings = account.get("strings");

        const orders = account.get("orders");

        return <div class="orders">
            {orders.map(order =>
                <div class="order" onClick={()=>this.openOrder(order.order)}>
                    <div class="top">
                        <h2>{strings.order}: {order.order}</h2>
                        <p>{order.date}</p>
                    </div>
                    <div class="description">
                        <p>{strings.value} {priceFormat(order.value)}</p>
                        <p>{strings.shippingMethod} {order.shipping}</p>
                        <div class="status">{strings.status}: {order?.status[order.status.length-1].name}</div>
                    </div>
                </div>    
            )}
        </div>
	}
}