import { Component, h } from '@stencil/core';
import { account } from "../../../global/data/account";

@Component({
    tag: 'ks-password-form',
    styleUrl: 'passwordForm.css'
})
export class PasswordForm {
	render() {
		const strings = account.get("strings");

        return [
            <p><ks-icon name="info" /> {strings.passwordFormMessage}</p>,
            <ks-input-text name="password_old"
                label={strings.passwordOld} required icon={"key"} right-icon
            />,
            <ks-input-text name="password_new"
                label={strings.passwordNew} required icon={"key"} right-icon
            />,
            <ks-input-text name="password_new_repeat"
                label={strings.passwordNewRepeat} required icon={"key"} right-icon
            />,
            <ks-button submit class="submit" round
                name={strings.passwordSubmit}
            />
        ];
	}
}










