


ks-account-order-modal .modal-wrapper {
    display: grid;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    justify-items: stretch;

    column-gap: 20px;

    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
        "top              top"
        "status-history   status-history"
        "shipping         payment"
        "products         products"
        "summary          summary"
        "buttons          buttons";

    width: 100%;
    height: 100%;
    max-width: 800px;

    margin: auto;
    overflow: auto;
}

ks-account-order-modal .modal-wrapper .top {
    grid-area: top;

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

ks-account-order-modal .modal-wrapper .status-history {
    grid-area: status-history;
    width: 100%;
    margin-bottom: 20px;
}

ks-account-order-modal .modal-wrapper .status-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;

    background-color: #f2f2f2;
    border-radius: 30px;
    padding-right: 20px;
}

ks-account-order-modal .modal-wrapper .status-row .status {
    min-width: 150px;
    background-color: #252525;
    color: #ffffff;
    padding: 5px 20px;
    border-radius: 30px;
    text-align: center;
}


ks-account-order-modal .modal-wrapper .shipping {
    grid-area: shipping;
}

ks-account-order-modal .modal-wrapper .payment {
    grid-area: payment;
}

ks-account-order-modal .modal-wrapper .shipping,
ks-account-order-modal .modal-wrapper .payment {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 20px;
}

ks-account-order-modal .modal-wrapper p {
    margin: 0;
}



ks-account-order-modal .modal-wrapper .products {
    grid-area: products;

    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 20px;
    margin-top: 20px;
    overflow: hidden;
}

ks-account-order-modal .modal-wrapper .products .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 8px 20px;
    border-bottom: 1px solid #ececec;
}

ks-account-order-modal .modal-wrapper .products .product {
    display: flex;
    justify-content: space-between;
    align-items: center;

    min-height: 60px;
    padding: 5px 20px;

    text-decoration: none !important;
    color: #252525 !important;

    background-color: #ffffff;
    transition: background-color 0.3s ease;
}

ks-account-order-modal .modal-wrapper .products .product:hover {
    background-color: #ececec;
}

ks-account-order-modal .modal-wrapper .products .product:active {
    background-color: #d8d8d8;
}

ks-account-order-modal .modal-wrapper .products .product:not(:last-child) {
    border-bottom: 1px solid #ececec;
}

ks-account-order-modal .modal-wrapper .products .image {
    grid-area: image;

    height: 100%;
    width: 100%;
    max-width: 60px;
    max-height: 60px;
    margin-right: 10px;
    mix-blend-mode: darken;
}

ks-account-order-modal .modal-wrapper .products .name {
    grid-area: name;
    width: 100%;
}

ks-account-order-modal .modal-wrapper .products .count {
    grid-area: count;
    width: 100%;
    max-width: 50px;
    text-align: center;
}

ks-account-order-modal .modal-wrapper .products .price {
    grid-area: price;
    width: 100%;
    max-width: 90px;
    text-align: right;
}


ks-account-order-modal .modal-wrapper .summary {
    grid-area: summary;

    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 20px;
    margin-top: 20px;
    overflow: hidden;
}

ks-account-order-modal .modal-wrapper .summary .entry {
    padding: 14px 20px;
    display: flex;
    justify-content: space-between;
}


ks-account-order-modal .modal-wrapper .summary .entry:not(:first-child) {
    border-top: 1px solid #ececec;
}

ks-account-order-modal .modal-wrapper .summary .entry.total {
    background-color: #252525;
    color: #ffffff;
    border-top: none;
    
    font-size: 19px;
    font-weight: 700;
    font-family: var(--font-emphasis);
    padding: 16px 20px;
}

ks-account-order-modal .modal-wrapper .buttons {
    grid-area: buttons;

    margin-top: 20px;
}


@media screen and (max-width: 640px) {
    ks-account-order-modal .modal-wrapper {
        display: block;
        font-size: 15px;
    }

    ks-account-order-modal .modal-wrapper .top {
        display: block;
        margin-bottom: 15px;
    }

    ks-account-order-modal .modal-wrapper .top h1 {
        margin-bottom: 5px;
        margin-top: 6px;
    }

    ks-account-order-modal .modal-wrapper .shipping {
        margin-bottom: 10px;
    }

    ks-account-order-modal .modal-wrapper .shipping {
        margin-bottom: 10px;
    }

    ks-account-order-modal .modal-wrapper .products .heading,
    ks-account-order-modal .modal-wrapper .products .product {
        display: grid;
        font-size: 13px;

        grid-template-columns: 60px 3fr 2fr;
        grid-template-areas: 
            "image name name"
            "image count price";
    
        grid-gap: 5px;
        align-content: center;
        align-items: center;
        justify-items: end;
    }

    ks-account-order-modal .modal-wrapper .products .product {
        padding: 12px 20px;
    }

    ks-account-order-modal .modal-wrapper .products .name,
    ks-account-order-modal .modal-wrapper .products .price {
        text-align: right;
    }
}