ks-account-return-dialog .form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

ks-account-return-dialog .form {
    width: 100%;
    max-width: 800px;
}

ks-account-return-dialog .form h1 {
    text-align: center;
}

ks-account-return-dialog .form .submit {
    margin-top: 20px;
}

ks-account-return-dialog .form .return_order_preselected {
    text-align: center;
    margin: -10px 0 10px 0;
}

@media screen and (max-width: 640px) {
    ks-account-return-dialog .form h1 {
        text-align: left;
        margin-top: 8px;
        margin-bottom: 30px;
    }

    ks-account-return-dialog .form {
       display: flex;
       flex-direction: column;
       justify-content: space-between;
       height: 100%;
       padding: 0 10px;
    }
}