import { Component, h, State, Element } from '@stencil/core';
import { account } from "../../../global/data/account";

@Component({
    tag: 'ks-additional-adresses',
    styleUrl: 'additionalAdresses.css'
})
export class AdditionalAdresses {
    @Element() root: HTMLKsAdditionalAdressesElement;

    @State() active: number = 0;

    selectAddress(index: number) {
        this.active = index;

        const data = account.get("addresses")?.[index];
        this.setValue("address_firstname", data.firstname);
        this.setValue("address_lastname", data.lastname);
        this.setValue("address_company", data.company);
        this.setValue("address_phone", data.phone);
        this.setValue("address_street", data.street);
        this.setValue("address_city", data.city);
        this.setValue("address_zip", data.zip);

        const option = data.country;
        (this.root.querySelector(`.edit-form select[name="address_country"] option[value="${option}"]`) as HTMLOptionElement)
            .selected = true;
    }

    setValue(name: string, value: string) {
        (this.root.querySelector(`.edit-form input[name="${name}"]`) as HTMLInputElement).value = value ?? "";
    }

    showAddDialog() {
        const dialog = this.root.querySelector("ks-dialog.add-dialog") as HTMLKsDialogElement;
        dialog.show();
    }

    showEditDialog(index: number) {
        this.selectAddress(index);

        const dialog = this.root.querySelector("ks-dialog.edit-dialog") as HTMLKsDialogElement;
        dialog.show();
    }

	render() {
		const strings = account.get("strings");
        const addresses = account.get("addresses");

        return [
            <div class="addresses">
                {addresses.map((address, index) => 
                    <div class="address" onClick={()=>this.showEditDialog(index)}>
                        <div class="name">
                            {address.company ? address.company : `${address.firstname} ${address.lastname}`}
                            <span class="dotseparator"> • </span>
                            {address.street} {address.city}
                        </div>
                        <div class="edit">
                            <ks-icon name="edit" />
                        </div>
                    </div>
                )}

                {addresses.length == 0 ? 
                    <div class="nocontent">{strings.noAddresses}</div>
                : null}
            </div>,

            <ks-button round left icon="plus" name={strings.addNewAddress} onClick={()=>this.showAddDialog()} />,

            <ks-dialog full class="edit-dialog">
                <div class="form-wrapper">

                    <h1>{strings.editAddress}</h1>
                    <div class="form edit-form">
                        {this.form()}
                        <ks-button submit class="remove" secondary round
                            name={strings.addressRemove}
                        />
                    </div>
                </div>
            </ks-dialog>,

            <ks-dialog full class="add-dialog">
                <div class="form-wrapper">

                    <h1>{strings.addNewAddress}</h1>
                    <div class="form add-form">
                        {this.form()}
                    </div>
                </div>
            </ks-dialog>
        ];
	}
    
    form() {
        const strings = account.get("strings");
        const countries = account.get("countries");

        return [
            <div class="left">
                <ks-input-text name="address_firstname"
                    label={strings.firstname} icon={"user"} right-icon
                />

                <ks-input-text name="address_lastname"
                    label={strings.lastname} icon={"user"} right-icon
                />

                <ks-input-text name="address_company"
                    label={strings.company} icon={"briefcase"} right-icon
                />

                <ks-input-text name="address_phone"
                    label={strings.phone} icon={"phone"} right-icon
                />
            </div>,

            <div class="right">
                <ks-input-text name="address_street"
                    label={strings.street} icon={"map-pin"} right-icon
                />

                <ks-input-text name="address_city"
                    label={strings.city} icon={"map-pin"} right-icon
                />

                <ks-input-text name="address_zip"
                    label={strings.zip} icon={"map-pin"} right-icon
                />

                <ks-input-select name="address_country"
                    label={strings.country}
                >
                    {countries.map(country =>
                        <option value={country.id} selected={country.id == 0}>
                            {country.name}
                        </option>    
                    )}
                </ks-input-select>
            </div>,

            <ks-button submit class="submit" round
                name={strings.submit}
            />
        ];
    }
}










