ks-points-overview {
    display: grid;
    align-items: center;

    grid-gap: 10px;
    grid-template-columns: 1fr auto;
    grid-template-areas: 
        "date    sum"
        "details sum";

    max-width: 600px;
}

ks-points-overview .details {
    grid-area: details;
}

ks-points-overview h2 {
    grid-area: date;
    margin: 0;
}

ks-points-overview p {
    margin: 0 0 3px 0;
    font-size: 0.9em;
}

ks-points-overview p .detail-value {
    font-weight: 700;
}

ks-points-overview .sum {
    grid-area: sum;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    overflow: hidden;

    padding: 20px;
    border-radius: 100px;

    height: 90px;
    width: 90px;
    font-size: 1.7em;
    line-height: 1.7em;

    font-weight: 700;
    font-family: var(--font-emphasis);

    background-color: #002448;
    color: white;
}

ks-points-overview .sum svg {
    position: absolute;
    bottom: -20px;
    width: 600%;
    animation: wave 100s infinite ease-in-out;
}

ks-points-overview .sum svg:nth-child(2) {
    animation-duration: 140s;
}

ks-points-overview .sum svg:nth-child(1) path {
    fill: rgba(0, 84, 177, 0.29);
}

ks-points-overview .sum svg:nth-child(2) path {
    fill: rgba(23, 64, 166, 0.29);
}   

@keyframes wave {
    0% { transform: translateX(-40%) }
    50% { transform: translateX(40%) }
    100% { transform: translateX(-40%) }
}

ks-points-overview .sum .points {
    z-index: 10;
}


@media screen and (max-width: 640px) {
    ks-points-overview {
        grid-template-areas: 
            "date    date"
            "sum     sum"
            "details details";

        justify-items: center;
        text-align: center;
        grid-gap: 20px;
    }

    ks-points-overview .sum {
        padding: 10px;
        width: 120px;
        height: 120px;

        font-size: 1.8em;
        line-height: 1.8em;
    }
}
