import { Component, h, Element } from '@stencil/core';
import { account, AccountOrder } from "../../../global/data/account";

@Component({
    tag: 'ks-account-products',
    styleUrl: 'accountProducts.css'
})
export class AccountProducts {
    @Element() root: HTMLElement;

	render() {
        const orders = account.get("orders");

        const products = orders.reduce((previous, current) => {
            return previous.concat(...current.products);
        }, [] as AccountOrder["products"]);
        
        const productMap = new Map(products.map(p => [p.id, p]));

        return [
            <div class="products">
                {[...productMap.values()].map(product => 
                    <a href={product.link} class="product">
                        <ks-img2 center class="image" src={product.image}/>
                        <div class="name">{product.name}</div>
                    </a>  
                )}
            </div>
        ];
	}
}