ks-account-orders {
    display: block;
}

ks-account-orders .orders{
    max-width: 900px;
}

ks-account-orders .order {
    border: #ececec 1px solid;
    padding: 20px;
    border-radius: 20px;
    user-select: none;
    margin-bottom: 20px;

    background-color: #ffffff;
    transition: background-color 0.3s ease;
}

ks-account-orders .order:hover {
    background-color: #ececec;
}

ks-account-orders .order:active {
    background-color: #dadada;
}

ks-account-orders .order .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

ks-account-orders .order .top * {
    margin: 0;
}

ks-account-orders .order .description {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

ks-account-orders .order .description * {
    margin: 0;
}

ks-account-orders .status  {
    background-color: #252525;
    color: #ffffff;
    padding: 5px 20px;
    border-radius: 30px;
    min-width: 90px;
    text-align: center;
}


@media screen and (max-width: 640px) {
    ks-account-orders .order .description {
        display: block;
        text-align: center;
    }

    ks-account-orders .order .description .status  {
        margin-top: 10px;
    }
}
