ks-account-returns {
    display: block;
    max-width: 800px;
}

ks-account-returns .entry {
    border: #ececec 1px solid;
    padding: 20px;
    border-radius: 20px;
    user-select: none;
    margin-bottom: 20px;

    background-color: #ffffff;
    transition: background-color 0.3s ease;
}

ks-account-returns .entry:hover {
    background-color: #ececec;
}

ks-account-returns .entry:active {
    background-color: #dadada;
}

ks-account-returns .entry .top {
    margin-bottom: 10px;
}

ks-account-returns .entry .top * {
    margin: 0;
}

ks-account-returns .entry .description {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

ks-account-returns .entry .description * {
    margin: 0;
}

ks-account-returns .status  {
    background-color: #252525;
    color: #ffffff;
    padding: 5px 20px;
    border-radius: 30px;
    min-width: 90px;
    text-align: center;
}

ks-account-returns .status.order  {
    cursor: pointer;
    margin-right: -5px !important;
}


ks-account-returns .return-modal-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    width: 100%;
    height: 100%;
    max-width: 800px;

    margin: auto;
}

ks-account-returns .return-modal-wrapper .flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    
    margin-bottom: 10px;
    padding: 0px 5px;
    box-sizing: border-box;
}

ks-account-returns .return-modal-wrapper .flex * {
    margin: 0;
}

ks-account-returns .return-modal-wrapper .description {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 20px;
    margin: 10px 0px 30px 0px;
}

ks-account-returns .return-modal-wrapper .status-history {
    width: 100%;
}

ks-account-returns .return-modal-wrapper .status-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;

    background-color: #f2f2f2;
    border-radius: 30px;
    padding-right: 20px;
}

ks-account-returns .return-modal-wrapper .status-row .status {
    min-width: 150px;
}

ks-account-returns .return-modal-wrapper .status-history {
    grid-area: status-history;
    width: 100%;
    margin-bottom: 20px;
}

ks-account-returns .return-modal-wrapper .status-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;

    background-color: #f2f2f2;
    border-radius: 30px;
    padding-right: 20px;
}

ks-account-returns .return-modal-wrapper .status-row .status {
    min-width: 150px;
}

@media screen and (max-width: 640px) {
    ks-account-returns .entry  {
        text-align: center;
    }

    ks-account-returns .entry .description {
        display: block;
    }

    ks-account-returns .entry .description .status  {
        margin-top: 10px;
    }


    ks-account-returns .return-modal-wrapper {
        font-size: 14px;
    }

    ks-account-returns .return-modal-wrapper .flex {
        display: block;
        margin-bottom: 10px;
        text-align: center;
    }
    
    ks-account-returns .return-modal-wrapper .flex * {
        margin: 0 0 8px 0;
    }
}
