ks-points-history {
    display: block;
    max-width: 800px;
}

ks-points-history .row {
    display: flex;
    align-items: stretch;
    font-size: 0.9em;
}


ks-points-history .row > * {
    display: flex;
    justify-content: center;
    align-items: center;

    color: black;
    text-decoration: none;
    flex: 1;
    text-align: center;
    margin-bottom: 7px;
}

ks-points-history .row > *:first-child {
    flex: 0.5;
    justify-content: flex-start;
    text-align: left;
}

ks-points-history .row-heading {
    font-size: 1em;
    font-weight: 700;
    font-family: var(--font-emphasis);
    margin-bottom: 10px;
}

ks-points-history .row > *:nth-child(3),
ks-points-history .row > *:nth-child(4) {
    flex: 0.8;
}

ks-points-history .row > *:nth-child(2),
ks-points-history .row > *:nth-child(5) {
    padding: 0px 20px;
}

ks-points-history .row:not(.row-heading) > *:nth-child(2),
ks-points-history .row:not(.row-heading) > *:nth-child(5) {
    background-color: black;
    color: white;
    border-radius: 20px;
    padding: 5px 10px;
}

ks-points-history .row .success {
    background-color: var(--color-green) !important;
}
ks-points-history .row .pending {
    background-color: var(--color-yellow-darker) !important;
}
ks-points-history .row .failed {
    background-color: var(--color-secondary-darker) !important; 
}

ks-points-history .order {
    cursor: pointer;
}

@media screen and (max-width: 640px) {
    ks-points-history .row > *:nth-child(2) {
        margin-right: 5px;
    }

    ks-points-history .row > *:nth-child(3),
    ks-points-history .row > *:nth-child(4) {
        display: none;
    }

    ks-points-history .row > *:nth-child(2),
    ks-points-history .row > *:nth-child(5) {
        padding: 0px 10px;
    }
}

@media screen and (max-width: 420px) {
    ks-points-history .row {
        font-size: 14px;
    }
}