ks-account-products .products {
    grid-area: products;
    max-width: 900px;

    width: 100%;
}

ks-account-products .products .product {
    display: flex;
    justify-content: space-between;
    align-items: center;

    min-height: 60px;
    padding: 5px 20px;

    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 20px;

    margin-bottom: 8px;

    text-decoration: none !important;
    color: #252525 !important;

    background-color: #ffffff;
    transition: background-color 0.3s ease;
}

ks-account-products .products .product:hover {
    background-color: #ececec;
}

ks-account-products .products .product:active {
    background-color: #d8d8d8;
}

ks-account-products .products .image {
    height: 100%;
    width: 100%;
    max-width: 60px;
    max-height: 60px;
    margin-right: 10px;
    mix-blend-mode: darken;
}

ks-account-products .products .name {
    width: 100%;
}

ks-account-products .products .count {
    width: 100%;
    max-width: 50px;
    text-align: center;
}

ks-account-products .products .price {
    width: 100%;
    max-width: 90px;
    text-align: right;
}