ks-additional-adresses {
    display: block;
    max-width: 800px;
}

ks-additional-adresses .addresses {
    margin-bottom: 20px;
}

ks-additional-adresses .nocontent {
    align-self: center;
    margin-right: 20px;
}

ks-additional-adresses .address {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    min-width: 60px;
    
    background-color: #ffffff;
    border: 1px #e5e5e5 solid;
    border-radius: 20px;
    margin-bottom: 10px;
    
    overflow: hidden;
    cursor: pointer;

    transition: background-color 0.3s ease;
}

ks-additional-adresses .address:hover {
    background-color: #fafafa;
}

ks-additional-adresses .address:active {
    background-color: #e5e5e5;
}

ks-additional-adresses .address .name {
    padding: 15px;
    width: 100%;
}

ks-additional-adresses .address .dotseparator {
    margin: 0 5px;
    color: #cfcfcf;
}

ks-additional-adresses .edit {
    padding: 15px;
}

ks-additional-adresses .add {
    padding: 15px;
    border: 1px solid #e5e5e5;
    border-radius: 50px;
    align-self: center;

    background-color: #ffffff;
    transition: background-color 0.3s ease;
}

ks-additional-adresses .add:hover {
    background-color: #fafafa;
}

ks-additional-adresses .add:active {
    background-color: #e5e5e5;
}

ks-additional-adresses .form {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-areas: 
        "left   right  right"
        "submit submit remove";

    grid-gap: 20px;

    width: 100%;
    max-width: 800px;
}

ks-additional-adresses .add-form {
    grid-template-areas: 
        "left   right  right"
        "submit submit submit";
}

ks-additional-adresses .form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: auto;

    width: 100%;
    height: 100%;
}

ks-additional-adresses .form-wrapper h1 {
    margin-bottom: 30px;
}

ks-additional-adresses .left {
    grid-area: left;
}

ks-additional-adresses .right {
    grid-area: right;
}

ks-additional-adresses .submit {
    grid-area: submit;
}

ks-additional-adresses .remove {
    grid-area: remove;
}



@media screen and (max-width: 640px) {
    ks-additional-adresses .form {
        grid-template-columns: 3fr 1fr;
        grid-template-areas: 
            "left   left"
            "right  right"
            "submit remove";
    
        grid-gap: 20px 10px;
    }
    
    ks-additional-adresses .add-form {
        grid-template-areas: 
            "left   left"
            "right  right"
            "submit submit";
    }
    
    ks-additional-adresses .form-wrapper {
        padding: 10px;
        display: block;
    }

    ks-additional-adresses .form-wrapper {
        padding: 10px;
        display: block;
    }

    ks-additional-adresses .form-wrapper h1 {
        margin: 7px 0 20px 0;
    }

    ks-additional-adresses .form-wrapper .submit {
        margin-bottom: 10px;
    }
}